body {
  font-family: Dm sans, sans-serif;
}

* {
  margin: 0;
  padding: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

.container {
  max-width: 480px;
  margin: 0 auto;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
  }
}

.button {
  min-height: 40px;
  text-align: center;
  border: none;
  border-radius: 20px;
}

.button--white {
  color: #d41443;
  background-color: #fff;
}

.button--white:hover, .button--white:focus {
  background-color: #ecebeb;
}

.button--main {
  color: #fff;
  background-color: #d41443;
}

.button--main:hover, .button--main:focus {
  background-color: #c7133e;
}

.header__container {
  padding: 6px, 20px, 36px, 20px;
  display: flex;
}

.header__wrapper {
  justify-content: space-between;
  display: flex;
}

.header__nav-items {
  gap: 23px;
  display: flex;
}

.header__nav-item {
  display: block;
}

.header__link {
  font-size: 16px;
  font-weight: 700;
}

.header__link:hover, .header__link:focus {
  color: #d41443;
}

.menu-container {
  padding: 60px, 21px;
  background-color: #e17992;
}

.menu-container__btn {
  width: 100%;
}

.menu-container__items {
  gap: 23px;
  display: flex;
}

.menu-container__item {
  border-top: 1px solid #e18292;
  border-bottom: 1px solid #e18292;
  display: block;
}

.menu-container__link {
  font-weight: 700;
}

.menu-container__link:hover, .menu-container__link:focus {
  color: #d41443;
}

.menu-toggle {
  background-color: #0000;
  border: none;
}

.menu-toggle__btn {
  fill: #d41443;
}

.menu-toggle__btn-close {
  color: #fff;
}

